___CSS_LOADER_EXPORT___.push([module.id, `.jp-Deck-Layover {
  position: absolute;
  inset: 0;
  background-color: rgba(0, 0, 0, 0.06);
  pointer-events: none;
}

.jp-Deck-Layover .jp-deck-mod-dragging {
  z-index: var(--jp-deck-z-layover);
  cursor: grabbing;
}

.jp-Deck-Layover .jp-Deck-LayoverPart {
  position: absolute;
  pointer-events: all;
  border: solid 2px var(--jp-deck-layover-color);
  opacity: 0.25;
  cursor: grab;
}

.jp-Deck-Layover .jp-Deck-LayoverPart:hover,
.jp-Deck-Layover .jp-Deck-LayoverPart:active {
  opacity: 0.8;
  transition: opacity 0.1s;
}

.jp-Deck-LayoverHandle {
  border: solid 1px var(--jp-deck-layover-color);
}

.jp-Deck-Layover .jp-Deck-LayoverPart .jp-Deck-LayoverHandle {
  position: absolute;
  width: var(--jp-deck-handle-size);
  height: var(--jp-deck-handle-size);
  opacity: 0.1;
  transition: opacity 0.1s;
}

.jp-Deck-Layover .jp-Deck-LayoverHandle:hover {
  opacity: 1;
  background-color: var(--jp-deck-layover-color);
  transition: opacity 0.1s;
}

.jp-Deck-Layover .jp-Deck-LayoverPart:hover .jp-Deck-LayoverHandle,
.jp-Deck-Layover .jp-Deck-LayoverPart:active .jp-Deck-LayoverHandle {
  opacity: 0.8;
  transition: opacity 0.1s;
}

/* centering */
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-n,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-s {
  left: 50%;
  margin-left: calc(-0.5 * var(--jp-deck-handle-size));
  cursor: ns-resize;
}

.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-e,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-w {
  top: 50%;
  margin-top: calc(-0.5 * var(--jp-deck-handle-size));
  cursor: ew-resize;
}

/* border and position */
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-n,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-nw,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-ne {
  top: 0;
  border-top: 0;
}

.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-s,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-sw,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-se {
  bottom: 0;
  border-bottom: 0;
}

.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-e,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-ne,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-se {
  right: 0;
  border-right: 0;
}

.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-w,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-nw,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-sw {
  left: 0;
  border-left: 0;
}

/** extra cursors */
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-ne,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-sw {
  cursor: nesw-resize;
}

.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-nw,
.jp-Deck-LayoverHandle.jp-Deck-LayoverHandle-se {
  cursor: nwse-resize;
}

/* index */

.jp-Deck-Layover .jp-Deck-LayoverUnstyle,
.jp-Deck-Layover .jp-Deck-LayoverLabel {
  line-height: 1.75;
  font-weight: bold;
  position: absolute;
  margin-top: calc(-0.5 * var(--jp-deck-handle-size));
  margin-left: calc(-0.5 * var(--jp-deck-handle-size));
  text-align: center;
  height: var(--jp-deck-handle-size);
  width: var(--jp-deck-handle-size);
  color: var(--jp-layout-color0);
}

.jp-Deck-Layover .jp-Deck-LayoverLabel {
  background-color: var(--jp-deck-layover-color);
  left: 50%;
  top: 50%;
  border-radius: var(--jp-deck-handle-size);
  border: solid 1px var(--jp-deck-layover-color);
}

.jp-Deck-Layover .jp-Deck-LayoverUnstyle {
  background-color: var(--jp-error-color1);
  border: none;
  left: 75%;
  margin-top: calc(-0.5 * var(--jp-deck-handle-size));
  top: 50%;
  cursor: pointer;
}
